import {
  CreateUserId,
  ProjectId,
  ProjectReportId,
  YearMonthDay,
} from '../value';

import { File } from '.';

export interface IBaseProjectReport {
  id: ProjectReportId;
  createUserId: CreateUserId;
  reportDate: YearMonthDay;
  projectId: ProjectId;
  files: File[];
}

export interface IUpdateBaseProjectReport {
  createUserId?: CreateUserId;
  reportDate?: YearMonthDay;
  files?: File[];
}

export abstract class BaseProjectReport {
  readonly id: ProjectReportId;
  readonly createUserId: CreateUserId;
  readonly reportDate: YearMonthDay;
  readonly projectId: ProjectId;
  readonly files: File[];
  constructor(args: IBaseProjectReport) {
    this.id = args.id;
    this.createUserId = args.createUserId;
    this.reportDate = args.reportDate;
    this.projectId = args.projectId;
    this.files = args.files;
  }

  abstract update(args: IUpdateBaseProjectReport): BaseUpdateProjectReport;
}

export abstract class BaseUpdateProjectReport {
  readonly id: ProjectReportId;
  readonly createUserId?: CreateUserId;
  readonly reportDate?: YearMonthDay;
  readonly files?: File[];
  constructor(args: IUpdateBaseProjectReport & { id: ProjectReportId }) {
    this.id = args.id;
    this.createUserId = args.createUserId;
    this.reportDate = args.reportDate;
    this.files = args.files;
  }
}
